import {DialogsItemsType, MessagesItemsType, PhotosType} from "../types/types";
import {BaseThunkType, InferActionsType} from "./reduxStore";
import {profileAPI} from "../API/profileAPI";

const ADD_MESSAGE = 'ADD-MESSAGE';
const DELETE_MESSAGE = 'DELETE_MESSAGE';
const AUTH_PHOTOS = 'AUTH_PHOTOS';


let initialState = {
    dialogsItems: [
        {id: 1, name: 'Tarzan', img:'data:image/jpeg;base64,'},
        {id: 2, name: 'Mowgli', img: 'data:image/jpeg;base64,'},
        {id: 3, name: 'Kaa', img: 'data:image/jpeg;base64,'},
        {id: 4, name: 'Bagheera', img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRSy8B0T00cp9PuUFiCmo-ag-IzBmlY3B3Ri_PMF0ZYht5olwK0&usqp=CAU'},
        {id: 5, name: 'Balu', img: 'data:image/jpeg;base64,'}
    ] as Array<DialogsItemsType>,
    messagesItems: [
        {id: 1, message: 'Yo, man!!!'},
        {id: 2, message: 'Hi, dude!!!'},
        {id: 3, message: 'What`s up?!'}
    ] as Array<MessagesItemsType>,
    textNewMessage: '',
    photos: {} as PhotosType

};
type InitialStateType = typeof initialState

const dialogsReducer = (state = initialState, action: DialogsActionsTypes): InitialStateType => {
    switch (action.type) {
        case ADD_MESSAGE:
            return {
                ...state,
                messagesItems: [...state.messagesItems, {id: state.messagesItems.length + 1, message: action.message}],
                textNewMessage: ''
            }
        case DELETE_MESSAGE:
            return {
                ...state,
                messagesItems: state.messagesItems.filter(m => m.id !== action.id)
            }
        case AUTH_PHOTOS:
            return {
                ...state,
                photos: action.photos
            }
        default:
            return state;
    }
}

type DialogsActionsTypes = InferActionsType<typeof dialogsActions>
type DialogsThunkType = BaseThunkType<DialogsActionsTypes>

export const dialogsActions = {
onAddMessage: (message: string) => ({type: ADD_MESSAGE, message} as const),
deleteMessage: (id: number) => ({type: DELETE_MESSAGE, id} as const),
setAuthPhotos: (photos: PhotosType) => ({type: AUTH_PHOTOS, photos} as const)
}
export const getAuthPhotosThunk = (userId: number): DialogsThunkType  => async (dispatch) => {
    let data = await profileAPI.getProfile(userId);
    dispatch(dialogsActions.setAuthPhotos(data.photos));
}

export default dialogsReducer;

